$(document).ready(function() {
	$('#fullpage').fullpage({
		anchors:['header', 'read', 'show', 'subscribe'],
		loopBottom: true,
		menu: '#menu',
		 afterLoad: function(anchorLink, index) {
    var loadedSection = $(this);

    if (index == 1) {
      $("header").hide();
    }

    if (index == 2) {
      $(".second h1").show();
      TweenMax.from("reads", 1, {opacity:0, left:"-20px", delay:0.5});
    }
  }
	});
});




